.navbar {
  width: 100%;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
  min-height: 50px;
  display: flex;
  align-items: center;
}
.navbar-full {
  width: 100vw;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
  min-height: 50px;
  display: flex;
  align-items: center;
}
.navbar input {
  border: none;
  padding: 20px 40px;
  width: 80%;
  background: none;
  outline: none;
  font-size: medium !important;
}
.navbar i {
  padding: 0 20px;
}
